/* dseg7-classic-mini-700-italic*/
@font-face {
  font-family: 'DSEG7';
  font-style: italic;
  font-display: swap;
  font-weight: 700;
  src:
    url(fonts/dseg7-classic-mini-700-italic.woff2) format('woff2'), 
    url(fonts/dseg7-classic-mini-700-italic.woff) format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
  
}
/* dseg7-classic-mini-700-normal*/
@font-face {
  font-family: 'DSEG7';
  font-style: normal;
  font-display: swap;
  font-weight: 700;
  src:
    url(fonts/dseg7-classic-mini-700-normal.woff2) format('woff2'), 
    url(fonts/dseg7-classic-mini-700-normal.woff) format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
  
}
